import React from "react";
import Layout from "../../../layouts/default";
import PageHeader from "../../../components/PageHeader";
import PageContent from "../../../components/PageContent";
import Loader from "../../../components/Loader";
import UserCard from "../../../components/UserCard";
import TwitterUserCard from "../../../components/TwitterUserCard";
import fetchTwitterProfile from "../../../utils/fetchTwitterProfile";
import fetchTwitterFollowers from "../../../utils/fetchTwitterFollowers";

let cachedTwitterProfile, cachedTwitterFollowers;

export default class Contributors extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      profile: {
        name: "The Bastion Bot",
        username: "TheBastionBot",
        followers_count: "Fetching",
      },
      followers: [],
    };
  }

  componentDidMount() {
    if (cachedTwitterProfile) {
      this.setState({
        profile: cachedTwitterProfile
      });
    }
    else {
      fetchTwitterProfile().then(fetchedTwitterProfile => {
        cachedTwitterProfile = fetchedTwitterProfile;

        this.setState({
          profile: fetchedTwitterProfile
        });
      }).catch(console.error);
    }

    if (cachedTwitterFollowers) {
      this.setState({
        followers: cachedTwitterFollowers
      });
    }
    else {
      fetchTwitterFollowers().then(fetchedTwitterFollowers => {
        cachedTwitterFollowers = fetchedTwitterFollowers;

        this.setState({
          followers: fetchedTwitterFollowers
        });
      }).catch(console.error);
    }
  }

  render () {
    return (
      <Layout
        title="Followers"
        description="These are all the people who follow @TheBastionBot on Twitter! Join them in following Bastion to stay updated on what's latest with Bastion."
        image=""
      >
        <PageHeader title="">
          <TwitterUserCard user={ this.state.profile } />
        </PageHeader>

        <PageContent>
          {
            this.state.followers.length
            ? this.state.followers.map((user, i) => (
                <UserCard
                  title={ user.name }
                  description={ user.username }
                  image={ "https://avatars.io/twitter/" + user.username + "/large" }
                  link={ "https://twitter.com/" + user.username }
                  key={ i }
                />
              ))
            : <Loader />
          }
        </PageContent>
      </Layout>
    );
  }
}
